const questions = document.querySelectorAll('[data-num]');
  
    let numberQuestions = 7;
    let currentQuestionNumber = 1;
  
    function showQuestion(n) {
      if (questions[currentQuestionNumber - 1].getAttribute('data-num') == 8) {
        document.querySelector('.question__buttons').style.display = 'none';
      }

      if (n > questions.length) {
        currentQuestionNumber = 1;
      }
  
      if (n < 1) {
        currentQuestionNumber = questions.length;
      }
  
      questions.forEach(item => {
        item.style.display = "none";
      });
  
      questions[currentQuestionNumber - 1].style.display = 'block';
    }
  
    showQuestion(currentQuestionNumber);
  
    function plusQuestion(n) {
      showQuestion(currentQuestionNumber += n);
    }
  
    function minusQuestion(n) {
      showQuestion(currentQuestionNumber -= n);
    }
  
    try {
      let nextBtn = document.querySelectorAll('.question-next');
  
      nextBtn.forEach(btn => btn.addEventListener('click', (e) => {
          e.preventDefault();
        plusQuestion(1);
        showQuestion(currentQuestionNumber);
      }));
    } catch (e) {}
  
  
    try {
      let prevBtn = document.querySelectorAll('.question-back');
  
      prevBtn.forEach(btn => btn.addEventListener('click', (e) => {
        e.preventDefault();
        minusQuestion(1);
        showQuestion(currentQuestionNumber);
      }));
    } catch (e) {}
  